.trademark-container{padding:2rem;font-family:"Segoe UI",sans-serif;background:#879099;color:#222}.trademark-container h1{text-align:center;color:#fff;font-size:2.5rem;margin-bottom:2rem}.trademark-container section{margin-bottom:2.5rem;background:#39707b;padding:2rem;border-radius:5px;box-shadow:0 6px 18px rgba(0,0,0,.08)}.trademark-container h2{color:#101010;margin-bottom:1rem;font-size:1.6rem}.trademark-container ul{list-style:none;padding-left:0}.trademark-container ul li{position:relative;padding-left:1.5rem;margin-bottom:.7rem;font-size:1rem}.trademark-container ul li::before{content:"✔";position:absolute;left:0;color:#0ceb13;font-weight:bold}.trademark-container .form-trademark-wrapper{border-radius:5px;display:flex;justify-content:space-between;gap:2rem;margin-bottom:2.5rem}.trademark-container .form-trademark-wrapper .form-section,.trademark-container .form-trademark-wrapper .why-trademark{flex:1;padding:2rem;border-radius:5px;box-shadow:0 6px 18px rgba(0,0,0,.08)}.trademark-container .form-trademark-wrapper form{display:flex;flex-direction:column;gap:1rem}.trademark-container .form-trademark-wrapper form input{padding:.9rem 1rem;border-radius:8px;border:1px solid #a7a6a6;color:#fff;font-size:1rem;transition:.2s;background:#39707b}.trademark-container .form-trademark-wrapper form input:focus{border-color:#4caf50;box-shadow:0 0 6px rgba(76,175,80,.3);outline:none}.trademark-container .form-trademark-wrapper form button{background:linear-gradient(45deg, #075a60, #018808);border:none;border-radius:5px;padding:.9rem;font-size:1rem;color:#fff;cursor:pointer;transition:.3s ease}.trademark-container .form-trademark-wrapper form button:hover{background:linear-gradient(45deg, #cbd116, #767934)}.trademark-container .pricing-docs{display:flex;flex-wrap:wrap;gap:2rem}.trademark-container .pricing-docs .pricing,.trademark-container .pricing-docs .documents{flex:1;background:linear-gradient(135deg, #879099, #39707b);padding:1.5rem;border-radius:5px;box-shadow:0 4px 12px rgba(0,0,0,.06)}.trademark-container .pricing-docs .pricing p,.trademark-container .pricing-docs .documents p{margin-bottom:.5rem;font-size:1rem}.trademark-container .steps{padding:1rem 2rem;background:#39707b;border-radius:5px}.trademark-container .steps h2{text-align:center;color:#fff;margin-bottom:2rem}.trademark-container .steps .reasons-steps{border-radius:5px;display:grid;grid-template-columns:repeat(auto-fit, minmax(320px, 1fr));gap:1.5rem}.trademark-container .steps .reasons-steps .step-card{background:linear-gradient(135deg, #879099, #39707b);padding:1.5rem;color:#ddd;border-radius:5px;box-shadow:0 4px 12px rgba(0,0,0,.05);text-align:center;font-size:1.1rem;font-weight:500}.trademark-container .steps .reasons-steps .step-card:hover{transform:scale(1.05);transition:all;box-shadow:0 6px 20px rgba(0,188,212,.4)}.trademark-container .advantages,.trademark-container .registration-types,.trademark-container .classes{padding:1rem 2rem;background:#39707b;border-radius:5px}.trademark-container .advantages h2,.trademark-container .registration-types h2,.trademark-container .classes h2{text-align:center;color:#fff;margin-bottom:2rem}.trademark-container .advantages .reasons-grid,.trademark-container .registration-types .reasons-grid,.trademark-container .classes .reasons-grid{border-radius:5px;display:grid;grid-template-columns:repeat(auto-fit, minmax(320px, 1fr));gap:1.5rem;justify-content:center;align-items:center}.trademark-container .advantages .reasons-grid .reason-card,.trademark-container .registration-types .reasons-grid .reason-card,.trademark-container .classes .reasons-grid .reason-card{background:linear-gradient(135deg, #879099, #39707b);padding:1.5rem;color:#ddd;border-radius:5px;box-shadow:0 4px 12px rgba(0,0,0,.05);text-align:center;font-size:1.1rem;font-weight:500}.trademark-container .advantages .reasons-grid .reason-card:hover,.trademark-container .registration-types .reasons-grid .reason-card:hover,.trademark-container .classes .reasons-grid .reason-card:hover{transform:scale(1.05);transition:all;box-shadow:0 6px 20px rgba(0,188,212,.4)}.trademark-container .faq-wrapper{background:#39707b;margin-bottom:2rem;border-radius:5px}.trademark-container .faq-wrapper h2{text-align:center;margin-bottom:30px;font-size:2rem;color:#222}.trademark-container .faq-wrapper .faq-section{display:flex;flex-direction:column;gap:25px}.trademark-container .faq-wrapper .faq-item{border-radius:8px;overflow:hidden;box-shadow:0 2px 8px rgba(0,0,0,.06)}.trademark-container .faq-wrapper .faq-question,.trademark-container .faq-wrapper .faq-answer{display:flex;align-items:flex-start;gap:15px;padding:18px 24px}.trademark-container .faq-wrapper .faq-question{background-color:#f1f1f1;border-left:5px solid #00a5a5}.trademark-container .faq-wrapper .faq-question h3{font-size:1.1rem;font-weight:600;color:#000;margin:0}.trademark-container .faq-wrapper .faq-answer{background-color:#fff}.trademark-container .faq-wrapper .faq-answer p{margin:0;font-size:1rem;color:#444}.trademark-container .faq-wrapper .faq-icon{font-size:1.4rem;font-weight:bold;width:30px;height:30px;min-width:30px;display:flex;align-items:center;justify-content:center;border-radius:50%}.trademark-container .faq-wrapper .faq-icon.q{background-color:#e0f2f1;color:#00a5a5}.trademark-container .faq-wrapper .faq-icon.a{background-color:#eee;color:#333}.trademark-container .cta{background:#39707b;border-radius:5px;display:flex;flex-direction:column;align-items:center;justify-content:center;padding:2rem;text-align:center;box-shadow:0 8px 20px rgba(0,0,0,.1)}.trademark-container .cta h2{color:#fff;font-size:2rem;font-weight:bold;margin-bottom:1rem;display:flex;align-items:center;justify-content:center}.trademark-container .cta p{font-size:1rem;color:#101010}@media(max-width: 768px){.trademark-container .form-trademark-wrapper{flex-direction:column}.trademark-container .form-trademark-wrapper .form-section,.trademark-container .form-trademark-wrapper .why-trademark{width:100%;margin-bottom:1.5rem}.trademark-container .pricing-docs,.trademark-container .steps{flex-direction:column}}