@import"https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap";.about-container{padding:2rem;margin:auto;font-family:"Inter",sans-serif;color:#f1f1f1}.about-container .intro h2{color:#fff}.about-container section{border-left:6px solid #1ab9ca;border-radius:5px;padding:2.5rem;margin-bottom:2rem;backdrop-filter:blur(1px);box-shadow:0 12px 32px rgba(0,0,0,.3);transition:transform .3s ease,box-shadow .3s ease}.about-container p{font-size:1.05rem;color:#e4e4e4;line-height:1.6;margin-bottom:1.2rem}.about-container ul{list-style:none;padding:0;margin-top:1.5rem}.about-container ul li{display:flex;align-items:flex-start;font-weight:500;color:#f1f1f1;position:relative;padding-left:2rem;margin-bottom:1rem;transition:color .3s ease}.about-container ul li::before{content:"✔";color:#0f747f;position:absolute;left:0;top:0;font-size:1.2rem;font-weight:bold}.about-container ul li:hover{color:#fff}@media(max-width: 768px){.about-container section{padding:1.8rem}.about-container h2,.about-container h3{font-size:1.5rem}.about-container ul li,.about-container p{font-size:.95rem}}